/* eslint-disable react-hooks/exhaustive-deps */
import { FC, useCallback } from 'react';
import { Scoreboard as ScoreboardComp } from '../../components/Scoreboard/Scoreboard';
import { RootState } from '../../pages/MineSweeperWithRedux/store';
import { useSelector, useDispatch } from 'react-redux';
import { GameLevels, LevelNames } from '../GameSettings';
import React from 'react';
import { actions } from './game';

const Scoreboard: FC = () => {
  const { level, time, bombs, flagCounter } = useSelector(
    ({ game: { level, time, bombs, flagCounter } }: RootState) => ({
      level,
      time,
      bombs,
      flagCounter,
    })
  );

  const dispatch = useDispatch();

  // useEffect(() => { if (urlParam) { dispatch(...) } }, [])
  const onChangeLevel = useCallback(
    ({ target: { value: level } }: React.ChangeEvent<HTMLSelectElement>) =>
      dispatch(actions.changeLevel(level as LevelNames)),
    []
  );

  const onReset = useCallback(() => dispatch(actions.reset()), []);

  return (
    <ScoreboardComp
      time={String(time)}
      mines={String(bombs - flagCounter)}
      levels={GameLevels as unknown as string[]}
      defaultLevel={level}
      onChangeLevel={onChangeLevel}
      onReset={onReset}
    />
  );
};

export default Scoreboard;
