/* eslint-disable react-hooks/exhaustive-deps */
import { FC, useCallback } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../pages/MineSweeperWithRedux/store';
import { actions } from './game';
import { Coords } from '../../core/Field';
import { Grid as GridComp } from '../../components/Grid/Grid';

export const Grid: FC = () => {
  const { playerField } = useSelector(
    ({ game: { playerField } }: RootState) => ({
      playerField,
    })
  );

  const dispatch = useDispatch();

  const onClick = useCallback(
    (coords: Coords) => dispatch(actions.openCell(coords)),
    []
  );

  const onContextMenu = useCallback(
    (coords: Coords) => dispatch(actions.setFlag(coords)),
    []
  );

  return (
    <GridComp onClick={onClick} onContextMenu={onContextMenu}>
      {playerField}
    </GridComp>
  );
};
