/* eslint-disable react-hooks/exhaustive-deps */
import { GameOver as GameOverComp } from '../../components/Game';
import { useSelector, useDispatch } from 'react-redux';
import { RootState } from '../../pages/MineSweeperWithRedux/store';
import { useCallback } from 'react';
import { actions } from './game';

const GameOver = () => {
  const { isGameOver, isWin } = useSelector(
    ({ game: { isGameOver, isWin } }: RootState) => ({
      isGameOver,
      isWin,
    })
  );

  const dispatch = useDispatch();

  const onReset = useCallback(() => dispatch(actions.reset()), []);

  return <>{isGameOver && <GameOverComp onClick={onReset} isWin={isWin} />}</>;
};

export default GameOver;
